@import url(../node_modules/semantic-ui-css/semantic.min.css);

@font-face {
    font-family: 'dinpro';
    src: local('dinpro'), url(../src/fonts/DINPro.otf) format('opentype');
  }
  @font-face {
    font-family: 'dinproBold';
    src: local('dinproBold'), url(../src/fonts/DINPro-Bold.otf) format('opentype');
  }
  @font-face {
    font-family: 'dinprolight';
    src: local('dinprolight'), url(../src/fonts/DINPro-Light.otf) format('opentype');
  }

  body {
    font-family: 'dinpro';
}
.imageDiv{
  position: absolute;
  width: 70%;
  height: auto;
  top: 23%;
  left: 25%;
  background-color: #00A1DF;
  z-index: 999;
}
.applyoptioncss{

background-color: #D3F0FD;
border:1px solid #00A1DF;
}
.grid-container {
    display: inline-flex;
    width: 100%;
    background-color: #2196F3;
    height:100%;
  }
  
  .grid-item {
    background-color: #fff;
    
    padding: 20px;
    font-size: 30px;
    
  }
  .firstcolumn{
      width:25%;
      background: #D3F0FD;
      padding: 20px 50px;
      overflow-y: auto;
  }
  .secondcolumn{
    width:65%;
    display: flex;
    align-items: center;    
  }
  .thirdcolumn{
    width:25%;
    display: flex;
    align-items: center;
  }
  .scroll_inst{
      color:#00044C;
      font-size: 18px;
      font-family: 'dinpro';
      opacity: 0.82;
  }
   .question_img img{
    max-width: 100%;
  }
  .imageDiv img{
    max-width: 100%;
  }
.rectbox_active{
    background-color:#00044C;
color:#fff;
 width: 40px;
 height:40px;
  }
  #root, main, .header{
    height:100%;
  }
  .quiz_section{
    height: calc(100% - 120px);
  }
  .tocModNo{
    width: 72px;
    height: 50px;
    background: #fff;
    color: #00044C;
    font-size: 25px;
    border-radius: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
    float:left;
    position:relative;
  }
  .tocModCover{
    display: table;
    padding: 10px 0;
    position: relative;
  }
  .toctxtCont{
    display: inline-block;
    float: right;
    width: 72%;
  }
  .tocTitle{
    font-size:25px;
    color:#707070;
    line-height:28px;
    font-weight:700;
    margin-bottom: 7px;
  }
  .tocDis{
    color:#161616;
    font-size:18px;
    line-height:22px;
  }
  .tocModCover.active .tocModNo{
    background: #00044C;
    color: #FFFFFF;
  }
  .tocModCover.acitve .tocTitle{
    color:#00044C;
  }

  .tocArea{
    position: relative;
    margin-top:10px;
    padding:10px 0;
    overflow-y:auto;
  }
  .verticalLine{
    position: absolute;
    border: 1px solid #707070;
    height: 46%;
    left: 33px;
    top: 70px;
  }
  .radiogrp {
    border: 1px solid #C5C5C5;
    font-size: 17px;
    color: #000000;
    margin-bottom: 12px;
}
.radiogrp label{
  padding: 15px;
  display: block;
  cursor: pointer;
}
.radiogrp input{
  display:none;
}
.secondcolumn .divided{
  width:100%;
}



.user{
	background:#f5fcff;
}
.recomSec{
  color: #00044C;
  font-size: 18px;
  padding: 35px 20px;
  margin: 0;
}
.moduleBox{
	padding: 20px;
	border-bottom:1px solid #ddd;
}
.moduleBox.active{
  background:#fff;
}
.moduleTit{
	font-size: 25px;
    color: #707070;
    font-weight: 700;
    margin-bottom: 2px;
}
.moduleDis{
	font-size:18px;
	color:#00044C;
	margin-bottom:0;
}
.screenInd {
  background: #FFFFFF;
  margin: 15px 0px;
}
.screenInd .screenTitle{	
    margin-bottom: 4px;
    text-transform: uppercase;
    font-size: 25px;
	font-weight:700;
  line-height:38px;
  color:#000000;
}
.greenTxt{
	color: #34ae73;
}
.redTxt{
	color: #ff5442;
}
.redTxt_2{
  color:#E8122F;
}
.yellowTxt{
	color: #ede44d;
}
.screenInd ul{
	list-style-type: none;
    margin: 0;
    padding: 0;
}
.screenInd ul a{
	text-decoration: none;
    color: #383838;
    font-size: 15px;
    text-transform: capitalize;
}
.screenDes{
	font-size:18px;
}
.suggestionArea{
	/*height: 680px;
    overflow-y: auto;*/
    padding-bottom: 20px;
    padding: 20px;
}
.firstResultcol {
  width: 25%;
  background: #D3F0FD;
  padding: 0;
  overflow-y: auto;
}

.secondResultcol {
  width: 75%;
  /*display: flex;*/
  align-items: center;  
  background: #eeeeee;
}
.w-50 {
  width: 50%;
  overflow-y: auto;
}
.resultHeader{
  background:#D3F0FD;
  padding:10px 0;
}
.resTit{
  font-size: 28px;
  color: #000000;
  margin-bottom: 7px;
  font-weight:700;
}
.resDis{
  color:#707070;
  font-size:20px;
}
.resultHeader .w-50{
  background: #D3F0FD;
}
.DL_Btn{
  background:#00A1DF;
  font-size:21px;
  color:#FFFFFF;
  width:236px;
  height:68px;
  border:1px solid transparent;
  margin-right:12px;
}
.emailRep_Btn{
  background:#D3F0FD;
  border:1px solid #00A1DF;
  width:236px;
  height:68px;
  font-size: 21px;
}
.txt-right{
  text-align: right;
}
.resultpageSec2{
  padding:50px 0;
}
.resultTable{
  margin:20px 0;
}
.resultTable td{
  width:110px;
  height: 52px;
  border-radius: 5px;
}
.grennBg{
  background:#009951;
}
.redBg{
  background:#E20000;
}
.yellow{
  background:#FFBD3D;
}
.mtCol{
  background:#F7F7F7;
  border:1px solid #9A9A9A;
}
.feedbackTitle{
  font-size:28px;
  color:#000;
  margin-bottom: 10px;
}
.feedbackdis{
  font: normal normal normal 18px/22px DIN Pro;
  letter-spacing: 0px;
  color: #000000;
  opacity: 0.6;
}
.questionFeedTit{
    padding: 25px 15px 25px 90px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-bottom: 1px solid #ddd;
    position:relative;
}
.questionFeedbody{
  padding: 15px 25px;
}
.catText{
  font-size:25px;
  line-height:38px;
  text-transform: uppercase;
  font-weight:700;
}
.questionBadge{
  width:58px;
  height:58px;
  border-radius:50%;
  position:absolute;
  left:15px;
  top:30px;
}
.redBg_2{
  background:#E8122F;
}
.userPopup{
  position: fixed;
  left:0;
  top:0;
  width:100%;
  height:100%;
  background: rgb(255 255 255 / 75%);
  z-index: 5;
  display:none;
}
.popUpArea{
  border: 1px solid #E1E1E1;
  background: #fff;
  box-shadow: 15px 15px 45px #4e4e4e4a;
  padding:60px 40px 50px;
  max-width: 990px;
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  margin: auto;
  height: 350px;
}
.emailInst{
  color:#00044C;
  font-size:28px;
  line-height:38px;
  font-weight:700;
  font-family: 'dinproBold';
}
.emailBox,  .emailBox:hover, .emailBox:focus, .emailBox:focus-visible{
  display: block;
  border: none;
  border-bottom: 1px solid #000;
  width: 90%;
  font-size: 20px;
  line-height: 38px;
  margin-top: 40px;
  margin-bottom: 40px;
}
.sendBtn{
  width:216px;
  height:60px;
  background:#00A1DF;
  color:#fff;
  font-size:20px;
  border:none;
}
.moduleClose{
  position: absolute;
  right: 22px;
  top: 30px;
  background: none;
  border: none;
  cursor: pointer;
}
.totalQues{
  color: #00A1DF;
  font-size: 20px;
  float: right;
  font-weight: 700;
}
.moduletitArea {
  font-size: 25px;
  font-weight: 700;
  border-bottom: 1px solid #000;
  padding-bottom: 15px;
}

@media only screen and (max-width: 1366px) {
  .radiogrp label {
    padding: 7px;
  }
  .radiogrp {
      font-size: 15px;
  }
  .questionPart {
    font-size: 18px;
    line-height: 21px;
  }
  .header_mulesoft {
    height: 70px;
  }
  
  .insTxt {
    color: #00A1DF;
    font-size: 17px;
    font-weight: 600;
    padding: 10px 0 17px;
  }
  .firstcolumn {
    padding: 15px 20px;
  }
  .tocModNo {
    width: 60px;
    height: 38px;
    font-size: 21px;
  }
  .tocTitle {
    font-size: 20px;
    line-height: 22px;    
  }
  .quiz_section {
      height: calc(100% - 70px);
  }
  .resTit {
    font-size: 20px;
  }
  .resDis {
      font-size: 18px;
  }
  .DL_Btn, .emailRep_Btn {    
    font-size: 16px;
    width: 205px;
    height: 55px;
  }
  .screenInd .screenTitle, .catText {
    font-size: 20px;
  }
  .screenDes {
    font-size: 16px;
  }
  .moduleTit {
    font-size: 18px;
  }
  .moduleBox {
    padding: 13px 20px;
  }
  .suggestionArea{
    overflow-y: auto;
    height:990px;
  }
}