html, body {
    height: 100%; 
}
@font-face {
    font-family: 'dinpro';
    src: local('dinpro'), url(../src/fonts/DINPro.otf) format('opentype');
  }
  @font-face {
    font-family: 'dinproBold';
    src: local('dinproBold'), url(../src/fonts/DINPro-Bold.otf) format('opentype');
  }
  @font-face {
    font-family: 'dinprolight';
    src: local('dinprolight'), url(../src/fonts/DINPro-Light.otf) format('opentype');
  }
  ::-webkit-scrollbar {
    width: 10px;
  }
  
  /* Track */
  ::-webkit-scrollbar-track {
    background: #f1f1f1;
  }
  body ::-webkit-scrollbar-thumb {
    cursor: pointer;
    border-radius: 5px;
    background: #00A1DF;
  }
.main_landingpage{
    height: auto !important;
    min-height: 100%;
    background: #00044C;
    position: absolute;
    width: 100%; 
    font-family: 'dinpro';
}
.right_txt{
    line-height: normal;
}
.landingpage {
    top: 30%;
    left: 17%;
    width: 67%;
    position: absolute;
}
.mulesoft_logo{
    text-align: center;
}
.mulesoft_logo img{
    width: 18%;
}
.mulesoft_logo2 img {
    width: 173px;
}
.mulesoft_logo2{
display:inline-block;  
text-align: center;
vertical-align: middle;
}
.headingright{
    display: inline-block;
    vertical-align: middle;
    margin-left: 10px;
    font-size: 30px;
    font-weight: 700;
}
.header_mulesoft{
    height: 120px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 12px;
}
.header_mulesoft .heading_left{
    color:#fff;
    float:left;
    font-size:20px;
    padding-top: 0;
}
.header_mulesoft .heading_right{
    color: #fff;
    float: right;
    font-size: 30px;
    margin-right: 33px;
    font-weight: 700;
}
.left_img{
    background-image: url("http://localhost:3000/public/assets/images/img.png");
}
.left_img img{
    width: 90%;
    height: 80%;
 
}
.desc{
    font-family: 'dinprolight';
    color:#fff;
    font-size: 23px;
    font-weight: normal !important;
}
.head1{
    color:#FFFFFF;
    font-size: 30px;
}
.head2{
    color:#00A1DF;
    font-size: 30px;
}
.start_btn{
    margin-top: 45px !important;
    left: 54px;
    width: 354px;
    height: 50px; 
    background: #00A1DF 0% 0% no-repeat padding-box !important;
    padding: 18px 112px 44px  101px!important;
    border-radius: 45px !important; 
    text-align: center !important;
    font-weight: normal !important;
    font-size:28px !important; 
    color:#fff !important; 
}
.left_img{
    float:left;
    width: 50%;
    height: 422px;
}
.insTxt{
    color:#00A1DF;
    font-size:20px;
    font-weight:600;
    padding: 15px 0 25px;
}
.questionPart{
    color:#000000;
    font-size:28px;
    line-height: 35px;
    font-weight:600;
}

@media only screen and (max-width: 1366px) {
    .header_mulesoft {
        height: 70px;
      }
      .headingright, .header_mulesoft .heading_right{
        font-size: 22px;
    }
    .questionPart {
        font-size: 18px;
        line-height: 21px;
      }
      .header_mulesoft {
        height: 70px;
      }
      
      .insTxt {
        color: #00A1DF;
        font-size: 17px;
        font-weight: 600;
        padding: 10px 0 17px;
      }
}